html {
    height: 100%;
}

.portfolio {
    text-align: center;
    height: 100%; 
    color: #343a40;
}

.background-img {
    height: 90vh;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
} 

.header-text { 
    text-align: center;
    z-index: 1;
    margin-top: -400px;
    margin-bottom: 300px;
} 

.text h1 {
    font-size: 50px;
} 

/* about section */

.about {
    height: 100%;
    width: 100%;
    background-color:  #cfcdc6;
    padding-top:50px;
} 

.header {
    padding-top: 135px;
    padding-bottom: 60px;
} 

.about-text { 
    background-color: #cfcdc6 ;
    display: flex;
    align-items: left;
    padding-bottom: 100px;
} 

.text {
    width: 70%;
    display: block;
    text-align: left;
    padding: 5%;
    border-right: 1px solid rgb(161, 158, 154) ;
    padding-bottom: 50px;
} 
 
.text h3 {
    padding-bottom: 10px;
}
.detail { 
    width: 30vw;
    justify-content: center;
   
} 

.contact {
    width: 100%;
    padding-top: 50px;
    
} 

